<template>
  <div id="minder-container"></div>
</template>
<script>
import kity from 'kity'
import kityminder from 'kityminder-core'

  export default {
    data () {
      return {
        data: {},
      }
    },
    mounted() {
      this.createKity();
    },
    methods: {
      createKity() {
        this.getSummary();
        var that = this;
        setTimeout(function() {
        var km = new window.kityminder.Minder({
          renderTo: '#minder-container'
        });
        km.importJson(that.data);
        }, 300);
      },
      getSummary() {
        this.$api.getSummary().then(res =>{
          if (res.result) {
            this.data = res.data;
          }
          }).catch((err)=>{
          console.log(err);
          });
        },
    }
  }
</script>

<style scoped>
  #minder-container {
    width: 100%;
    height: 100%;
  }
</style>


